import { createContext, useContext } from 'react';

export enum Theme {
    Dark = 'Dark',
    Light = 'Light',
}

export type DateSelectorType = {
  lootDate: Date;
  setLootDate: (lootDate: Date) => void;
  startDate: Date|null;
  setStartDate: (lootDate: Date|null) => void;
  endDate: Date | null;
  setEndDate: (endDate: Date | null) => void;
  isFilter: boolean;
  setIsFilter: (isFilter: boolean) => void;
  searchBar :string;
  setSearchBar: (searchBar: string) => void;
}

export const DateSelectorContext = createContext<DateSelectorType>({isFilter: false,searchBar:"",setSearchBar:searchBar => console.warn('no theme provider'), setIsFilter:lootDate => console.warn('no theme provider'),lootDate: new Date(), startDate:new Date(),endDate:null,setEndDate: lootDate => console.warn('no theme provider'),setStartDate: lootDate => console.warn('no theme provider'),  setLootDate: lootDate => console.warn('no theme provider') });

export const useDateSelector = () => useContext(DateSelectorContext);

