import React, { forwardRef, useEffect, useRef, useState } from "react";
import { NominatimData } from "./NominatimData";
import './Carousel.css';
import CaptureLogo from '../../Capture_Side_By_Side_White.svg';
import DatePicker, { ReactDatePickerProps } from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { useDateSelector } from "../../DateContext";
import LazyLoad from 'react-lazyload';
import { Tween, Timeline } from 'react-gsap';
import Viewer from 'react-viewer';

import { LazyLoadComponent, LazyLoadImage } from 'react-lazy-load-image-component';

export type ImageType = {
  id: any;
  url: any;
  parsed_meta: any;
  caption: any;
  updated_at: any;
  asset_file_thumbnail: any;
  asset_file_url: any;
  cai_file: any;
  cai_file_url: any;
  cid: any;
  creator: any;
  creator_name: any;
  time: any;
  lat: any;
  lon: any;
  original: any;
};

export type InfoType = {
  id: any;
  url: any;
  parsed_meta: any;
  caption: any;
  updated_at: any;
  asset_file_thumbnail: any;
  asset_file_url: any;
  cai_file: any;
  cai_file_url: any;
  cid: any;
  creator: any;
  creator_name: any;
  time: any;
  lat: any;
  lon: any;
  original: any;
};
const useProgressiveImage = (src: any) => {
  const [sourceLoaded, setSourceLoaded] = useState(null)

  useEffect(() => {
    const img = new Image()
    img.src = src
    img.onload = () => setSourceLoaded(src)
  }, [src])

  return sourceLoaded
}
const ImageCarousel: React.FC<{
  page?: string;
  images?: ImageType[];
  Infos?: ImageType[];
  total?: number;
}> = ({ images, Infos, page, total }) => {
  const [selectedImageIndex, setSelectedImageIndex] = useState(0);
  const [selectedImage, setSelectedImage] = useState<ImageType>();
  const [selectedInfo, setSelectedInfo] = useState<InfoType>();

  const carouselItemsRef = useRef<HTMLDivElement[] | null[]>([]);
  const { setLootDate, setEndDate, setStartDate, endDate, startDate, isFilter, setIsFilter, setSearchBar, searchBar } = useDateSelector();

  const [loaded, setLoaded] = React.useState(false);

  // const [isFilter, setIsFilter] = useState(false);
  // const [startDate, setStartDate] = useState<any>(new Date());
  const [visible, setVisible] = React.useState(false);

  const [query, setQuery] = useState("")

  const [searchValue, setSearchValue] = useState("")



  useEffect(() => {
    if (images && images[0]) {
      carouselItemsRef.current = carouselItemsRef.current.slice(
        0,
        images.length
      );

      setSelectedImageIndex(0);
      setSelectedImage(images[0]);
      setSelectedInfo(images[0]);
      //console.log("selectedInfo", selectedInfo);
    }
  }, [images]);

  const handleSelectedImageChange = (newIdx: number) => {
    if (images && images.length > 0) {
      setSelectedImage(images[newIdx]);
      setSelectedImageIndex(newIdx);
      // 錨點 自動滑動到照片
      if (carouselItemsRef?.current[newIdx]) {
        carouselItemsRef?.current[newIdx]?.scrollIntoView({
          inline: "center",
          behavior: "smooth",
          block: "end"
        });
      }
    }
  };

  //   Tammy  4:54 PM
  // @DaYuan 應該不是高度被影響到
  // 我的觀察是
  // 如果按箭頭，實際上的行為是 往下跳一張
  // 這個行為跟我任一點一張照片是一樣的
  // 換句話說，如果我隨便點一張照片，底下的放大照會跳上來，跳成 我點選的照片
  // 如果按上方照片列的左右箭頭，底下的放大照也會跳上來，跳成 下一張照片
  // 但上方照片列的箭頭被按下時，我們不要 跳下一張 ，我們要的是 上方照片列往右多顯示幾張

  const handleSelectedImageChange_no_Change_view = (newIdx: number) => {
    if (images && images.length > 0) {
      // setSelectedImage(images[newIdx]);
      setSelectedImageIndex(newIdx);
      // 錨點 自動滑動到照片
      if (carouselItemsRef?.current[newIdx]) {
        carouselItemsRef?.current[newIdx]?.scrollIntoView({
          inline: "center",
          behavior: "smooth",
          block: "end"
        });
      }
    }
  };

  ///10個一組 須改變  slice(start,offset)  {selectedInfo?.time.slice(0, 10)}
  const handleRightClick = () => {
    if (images && images.length > 0) {
      let newIdx = selectedImageIndex + 1;
      if (newIdx >= images.length) {
        newIdx = 0;
      }
      handleSelectedImageChange_no_Change_view(newIdx);
    }
  };

  const handleLeftClick = () => {
    if (images && images.length > 0) {
      let newIdx = selectedImageIndex - 1;
      if (newIdx < 0) {
        newIdx = images.length - 1;
      }
      handleSelectedImageChange_no_Change_view(newIdx);
    }
  };

  interface Props extends Omit<ReactDatePickerProps, 'onChange'> {
    onClick?(): void;
    onChange?(): void;
  }



  const ExampleCustomInput = ({ value, onClick }: { value: string; onClick: (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void }) => (
    <button className="content-Button " onClick={onClick} ><p className="view-all" > {value}</p></button>

  );

  return (
    <div className="carousel-container">
      <div className="carousel">
        <div className="carousel__images">
          {images &&
            images.map((image, idx) => (
              // <div
              //   onClick={() => handleSelectedImageChange(idx)}
              //   style={{ backgroundImage: `url(${image.url})` }}
              //   key={image.id}
              //   className={`carousel__image ${selectedImageIndex === idx && "carousel__image-selected"
              //     }`}
              //   ref={(el) => (carouselItemsRef.current[idx] = el)}
              // />
              <div
                onClick={() => handleSelectedImageChange(idx)}
                style={{ backgroundImage: `url(${image.url})` }}
                key={image.id}
                className={`carousel__image`}
                ref={(el) => (carouselItemsRef.current[idx] = el)}
              />
            ))}
        </div>
        <div className="btn-prev carousel__button carousel__button-left" onClick={handleLeftClick}>
          <svg width="15" height="24" viewBox="0 0 15 24" xmlns="http://www.w3.org/2000/svg"><title>Left Arrow</title><path d="M13 2.308l-10 10 10 10" stroke-width="4" stroke="#C3000B" fill="none" fill-rule="evenodd"></path></svg>
        </div>
        {/* <button
          className="carousel__button carousel__button-left"
          onClick={handleLeftClick}
        >
          Prev
        </button>
        <button
          className="carousel__button carousel__button-right"
          onClick={handleRightClick}
        >
          Next
        </button> */}
        <div className="btn-next carousel__button carousel__button-right" onClick={handleRightClick}>
          <svg width="15" height="24" viewBox="0 0 15 24" xmlns="http://www.w3.org/2000/svg"><title>Right Arrow</title><path d="M2 2.308l10 10-10 10" stroke-width="4" stroke="#C3000B" fill="none" fill-rule="evenodd"></path></svg>
        </div>
      </div>
      {/* <h2 className="header">讀者投稿</h2> */}
      <div className="category">
        {/* <div className="category-name">讀者投稿</div> */}
        <div className="category-line">
          {/* <div>
            {page} */}
          {/* 讀者投稿 */}
          {/* {loaded ? "t" : "f"} */}
          {/* </div> */}
          <div className="content-text">
            <p className="view-all" > {page} 目前為第{selectedImageIndex + 1}張/總共{total}張</p>
          </div>
          <div className="category-date">
            <input className="content-Button " placeholder="Enter Caption" type="text" value={searchValue} onChange={event => setSearchValue(event.target.value)} />
            {/* <input className="content-Button " placeholder="Enter Caption" type="text" value={searchBar } onChange={event => setSearchBar(event.target.value)}/> */}
            {/* { searchBar} */}
            <button className="content-Button " onClick={() => {
              // handleCalendarClose(date)
              // setStartDate(new Date())
              // setLootDate(new Date())
              setSearchBar(searchValue)
              //console.log(`搜尋此條件${searchValue}`);

            }}><p className="view-all" >搜尋</p>
            </button>

            <div className="content-text">
              <p className="view-all" >OR</p>
            </div>

            <div className="category-DatePicker">
              <DatePicker selected={startDate}
                customInput={React.createElement(ExampleCustomInput)}
                dateFormat="yyyy-MM-dd"
                // onCalendarClose={handleCalendarClose}
                startDate={startDate}
                endDate={endDate}
                selectsRange
                onChange={(dates) => {
                  // handleCalendarClose(date)
                  // setStartDate(date)
                  const [start, end] = dates;
                  if (dates) {
                    setStartDate(start);
                    setEndDate(end);
                    if (end != null) {
                      setIsFilter(true)
                      setSearchValue("");
                      setSearchBar("");
                    }
                  }

                  //console.log(dates)
                }} />
            </div>
            <button className="content-Button " onClick={() => {
              // handleCalendarClose(date)
              // setStartDate(new Date())
              // setLootDate(new Date())
              setSearchValue("");
              setSearchBar("");

              setStartDate(new Date());
              setEndDate(null);
              setIsFilter(true)
            }}><p className="view-all" >查看所有</p>
            </button>
          </div>
        </div>
        <div className="line-top"></div>

      </div>

      {/* <div
          onLoad={() => setLoaded(true)} 
            className="selected-image"
            style={{ backgroundImage: `url(${selectedImage?.asset_file_url})` }}
      /> */}
      <img className="originalimg" onLoad={() => setLoaded(true)} onClick={() => { setVisible(true); }} alt='origina_img' src={selectedImage?.original} />


      {/* {
        !loaded ?
          <div
            className="selected-image"
            style={{ backgroundImage: `url(${selectedImage?.url})` }}
          />
          : null
      } */}
      {/* {
        !loaded ?
          <div
            className="selected-image"
            style={{ backgroundImage: `url(${selectedImage?.url})` }}
          />
          : <div
          onLoad={() => setLoaded(true)} 
            className="selected-image"
            style={{ backgroundImage: `url(${selectedImage?.asset_file_url})` }}
          />
      } */}
      <Viewer
        scalable={false}
        noFooter={true}
        noNavbar={true}
        noToolbar={true}
        noImgDetails={true}
        visible={visible}
        onClose={() => { setVisible(false); }}
        images={[{ src: selectedImage?.asset_file_url, alt: '' }]}
      />
      {/* <div>
        <LazyLoadImage
          className="selected-image"
          alt={selectedImage?.cid}
          src={`url(${selectedImage?.asset_file_url})`}
          effect="blur"
        >

        </LazyLoadImage>
        <LazyLoadComponent>
          <div
            // onLoad={() => setLoaded(true)}
            className="selected-image"
            style={{ backgroundImage: `url(${selectedImage?.asset_file_url})` }}
          />
        </LazyLoadComponent>
      </div> */}

      {/* {selectedInfo[1]} */}
      <div className="directions">
        <div className="info">
          <div className="user-mini">
            {/* {"讀者投稿"} */}
            {page}

            <img src={CaptureLogo} className="Capture-logo-mini" alt="logo" />{" "}
          </div>
          <p className="time">日期 {" "}{selectedImage?.time.slice(0, 10)}</p>
          <p className="time">時間 {" "}{selectedImage?.time.slice(11, 19)}</p>
          <p className="location">
            <NominatimData lat={selectedImage?.lat} lon={selectedImage?.lon} />{" "}
          </p>
          <p className="GPS">
            {selectedImage?.lat === "undefined"
              ? ""
              : `GPS ${selectedImage?.lat}`}{" "}
            {selectedImage?.lon === "undefined" ? "" : selectedImage?.lon}
          </p>
          <p className="GPS">
            Web 3.0 影像 ID :{" "}
            <a
              target="_blank"
              href={"https://ipfs.io/ipfs/" + selectedImage?.id}
            >
              {selectedImage?.id}
            </a>
          </p>
          <p className="GPS">
            去中心化影像網絡註冊 :{" "}
            <a
              target="_blank"
              href={
                "https://authmedia.net/public-certificate?mid=" +
                selectedImage?.id
              }
            >
              {selectedImage?.id}
            </a>
          </p>
        </div>
      </div>
      <div className="caption">
        {selectedImage?.caption ? selectedImage?.caption : ""}
      </div>
    </div>
  );
};


export default ImageCarousel;
