  function findObjectByProporigin_A_Z(arr: any[], prop: string, val: { toISOString: () => string | any[]; }, end: { toISOString: () => string | any[]; }) {
    let result: any[] = [];
    // //console.log("startDate",val.toISOString().slice(0, 10));
    // //console.log("endDate",end.toISOString().slice(0, 10));
    let startDate_ = val.toString();
    let endDate_ = end.toString();
    let valTime = new Date(startDate_);
    let endTime = new Date(endDate_);
    //console.log("startDate_", startDate_, valTime);
    //console.log("endDate_", endDate_, endTime);
    if (!startDate_) { return [] }
    if (!endDate_) { return [] }

    arr.map((el: { [x: string]: string | any[] }) => {
      // //console.log(el[prop].slice(0, 10), val);

      //console.log("info", el[prop]);

      //console.log("VS_", valTime < endTime);

      // if (valTime < endTime !== true) { return }

      // if (el[prop].slice(0, 10) === val.toISOString().slice(0, 10)) {
      //   result.push(el);
      // }
      let data = el[prop].slice(0, 10);
      let dataTime = new Date(data.toString());
      //console.log(`${valTime}=<${dataTime}=<${endTime}TIME result`, valTime < dataTime && dataTime < endTime);
      //console.log(`${valTime}=<${endTime}TIME result`, valTime < endTime);
      //console.log(`${valTime}=<${dataTime}TIME result`, valTime < dataTime);
      //console.log(`${dataTime}=<${endTime}TIME result`, dataTime < endTime);

      if (valTime <= dataTime && dataTime <= endTime) {
        result.push(el);
      }
    });
    //console.log("result", result);

    return result;
  }
