import React, { useEffect, useRef, useState } from "react";

export const NominatimData = (props: { lon: string; lat: string }) => {
  const [nominatim, setNominatim] = useState("");
  //console.log(props.lon + "," + props.lat);

  // useEffect(() => {

  //     // fetchnominatimData('50.734310327145025','24.695296799787034');
  // }, []);

  const fetchData = async () => {
    if (props.lon == undefined) {
      return "";
    }
    try {
      const response = await fetch(
        `https://forward-reverse-geocoding.p.rapidapi.com/v1/reverse?lat=${props.lat}&lon=${props.lon}&accept-language=zh_tw&format=json&polygon_threshold=0.0`,
        {
          method: "GET",
          headers: {
            "x-rapidapi-host": "forward-reverse-geocoding.p.rapidapi.com",
            "x-rapidapi-key":
              "764fd260b3msh993f28723a63f85p1ea974jsnaa720e9e39db"
          }
        }
      );
      const json = await response.json();
      //console.log(json);
      setNominatim(
        json.address.country
          ? json.address.country
          : "" + "," + json.address.state
          ? json.address.state
          : "" + "," + json.address.state
          ? json.address.state
          : "" + "," + json.address.city
          ? json.address.city
          : ""
      );
    } catch (error) {
      //console.log("error", error);
    }
  };

  fetchData();
  return <span>{props.lon == "undefined" ? "" : "地點 " + nominatim}</span>;
};
